@import "~react-image-gallery/styles/scss/image-gallery.scss";

.body {
    margin: 0;
    padding: 0;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
    padding: 0;
}

* {
    box-sizing: border-box;
}

.lot {
    width: 1200px;
    margin: 0 auto;
    padding: 20px;
    hr {
        border: none;
        height: 1px;
        background-color: orange
    }
}

.title-region-wrapper {
    display: flex;
    justify-content: space-between;
    margin: 20px 0px;
}

.lot-title, .region-title  {
    font-size: 24px;    
}

.lot-title {
    color: rgba(128, 128, 128, 0.9)
}

.region-title {
    color: orange;
}

.lot-wrapper {
    border: 1px solid rgba(255, 166, 0, 0.233);
    display: flex;
    padding: 20px;
    background-color: rgba(255, 240, 199, 0.4);
}

.image-gallery {
    width: 320px;
    .image-gallery-left-nav svg, .image-gallery-right-nav svg {
        width: 50px;
        height: 50px;
    }
}

.description-contacts-wrapper {
    margin-left: 20px;
    display: flex;
    flex-direction: column;
}

.lot-description {
    font-style: italic;
    font-size: 22px;
    font-weight: 400;
    color: rgb(41, 170, 41)
}

.lot-text {
    font-size: 18px;
}

.price-qr-code-wrapper {
    margin-top: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.lot-price {
    font-weight: 700;
    font-size: 24px;
    span {
        color: rgb(41, 170, 41);
    }
    color: black
}

.lot-qr-code {
    font-size: 22px;
    cursor: pointer;
    border-bottom: 1px solid orange;
    color: orange;
    div {
        background-color: rgba(0, 0, 0, 0.486);
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        position: fixed;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.contacts {
    display: block;
    margin-top: auto;
    font-size: 18px;
    text-align: right;
    span {
        font-weight: 700;
        color: rgb(41, 170, 41)
    }
}